
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.eyes{
-webkit-perspective: 1000px;
        perspective: 1000px;
}
.box{
/*设置3D效果*/
-webkit-transform-style: preserve-3d;
        transform-style: preserve-3d;
/*盒子的大小*/
width: 200px;
height: 200px;
background: #fff;
background:url(./static/img/4.0b258ba.jpg);width: 200px; background-repeat:no-repeat;
/*设置盒子的位置，便于观察*/
margin: 400px auto;
/*复合方式设置动画 三者分别为：动画名 执行一次时间 执行方式*/
-webkit-animation: zhuan 3s ease;
        animation: zhuan 3s ease;
/*令动画无限执行下去*/
-webkit-animation-iteration-count: infinite;
        animation-iteration-count: infinite;
-webkit-animation-timing-function: linear;
        animation-timing-function: linear;
}
.box div{
width: 200px;
height: 200px;
opacity: 1;
/*设置过渡*/
-webkit-transition: all 1s ease 0s;
transition: all 1s ease 0s;
position: absolute;
}
/*调整位置，制作成一个六边形*/
.box .div1{
background: green;
-webkit-transform: translateZ(100px);
        transform: translateZ(100px);
}
.box .div2{
background: green;
-webkit-transform: translateZ(-100px);
        transform: translateZ(-100px);
}
.box .div3{
background: green;
-webkit-transform: rotateX(90deg) translateZ(100px);
        transform: rotateX(90deg) translateZ(100px);
}
.box .div4{
background: green;
-webkit-transform: rotateX(270deg) translateZ(100px);
        transform: rotateX(270deg) translateZ(100px);
}
.box .div5{
background: green;
-webkit-transform: rotateY(-90deg) translateZ(100px);
        transform: rotateY(-90deg) translateZ(100px);
}
.box .div6{
background: green;
-webkit-transform: rotateY(90deg) translateZ(100px);
        transform: rotateY(90deg) translateZ(100px);
}
/*添加3D旋转效果 让其绕X、Y轴同时旋转90度*/
@-webkit-keyframes zhuan{
from{
-webkit-transform: rotateX(0deg) rotateZ(0deg) rotateY(0deg);
        transform: rotateX(0deg) rotateZ(0deg) rotateY(0deg);
}
to{
-webkit-transform: rotateX(360deg) rotateZ(360deg) rotateY(360deg);
        transform: rotateX(360deg) rotateZ(360deg) rotateY(360deg);
}
}
@keyframes zhuan{
from{
-webkit-transform: rotateX(0deg) rotateZ(0deg) rotateY(0deg);
        transform: rotateX(0deg) rotateZ(0deg) rotateY(0deg);
}
to{
-webkit-transform: rotateX(360deg) rotateZ(360deg) rotateY(360deg);
        transform: rotateX(360deg) rotateZ(360deg) rotateY(360deg);
}
}
/*给正方体添加一个hover效果*/
.box:hover .div1{
-webkit-transform: translateZ(200px);
        transform: translateZ(200px);
}
.box:hover .div2{
-webkit-transform: translateZ(-200px);
        transform: translateZ(-200px);
}
.box:hover .div3{
-webkit-transform: rotateX(90deg) translateZ(200px);
        transform: rotateX(90deg) translateZ(200px);
}
.box:hover .div4{
-webkit-transform: rotateX(270deg) translateZ(200px);
        transform: rotateX(270deg) translateZ(200px);
}
.box:hover .div5{
-webkit-transform: rotateY(-90deg) translateZ(200px);
        transform: rotateY(-90deg) translateZ(200px);
}
.box:hover .div6{
-webkit-transform: rotateY(90deg) translateZ(200px);
        transform: rotateY(90deg) translateZ(200px)
}
